'use strict';

import $ from 'jquery';
import '../css/classroom.pcss';

$(function () {

  const DOC = $(document);
  const $modal = $('.js-modal');
  const $sliderItemBox = $('.js-slider-item-box');
  const $dropdownBox = $('.js-dropdown-box');
  const $notification = $('.js-notification');
  const $notificationDropdown = $('.js-notification-dropdown');
  const $user = $('.js-user');
  const $userDropdown = $('.js-user-dropdown');
  const $language = $('.js-language');
  const $languageDropup = $('.js-language-dropup');
  const $slideContainer = $('.js-slide-container');
  const $starSelect = $('.js-star-select');
  const $sliderContent = $('.js-slider-content');
  const $slideCell = $('.js-slide-cell');

  const isMobile = /Android|iPhone|SymbianOS|Windows Phone|iPad|iPod/.test(navigator.userAgent);

  if (isMobile) {
    $slideCell.css('width', document.documentElement.scrollWidth - 112);
  }

  DOC.on('click', '.js-cancel-class', function (e) {
    $modal.show();
  }).on('click', '.modal-btn .btn, .modal-close', function (e) {
    $modal.hide();
  }).on('click', '.slider-item-btn', function (e) {
    $sliderItemBox.toggleClass('in');
  }).on('click', '.dropdown', function (e) {
    $dropdownBox.toggleClass('in');
  }).on('click', '.js-arrow-left', function (e) {
    $slideContainer.clearQueue().finish();
    const w = $slideContainer.children('.slide-cell')[0].clientWidth + 1;
    if ($slideContainer.position().left === 0) {
      return false;
    }
    $slideContainer.animate({
      left: `+=${w}`
    });
  }).on('click', '.js-arrow-right', function (e) {
    $slideContainer.clearQueue().finish();
    const left = Math.abs($slideContainer.position().left);
    const width = $slideContainer.parent().width();
    const parent_width = ($slideContainer.children('.slide-cell').length - 1) * 266;
    const w = $slideContainer.children('.slide-cell')[0].clientWidth + 1;
    if (left !== 0 && left + width > parent_width) {
      return false;
    }
    $slideContainer.stop().animate({
      left: `-=${w}`
    });
  }).on('click', '.js-toggle-chat', function (e) {
    $sliderContent.slideToggle();
  });

  $notification.on('mouseenter', function () {
    if (isMobile) return false;
    $notificationDropdown.show();
  }).on('mouseleave', function () {
    $notificationDropdown.hide();
  }).on('click', 'ul li a', function (e) {
    $(e.target).removeClass('new');
  });

  $user.on('mouseenter', function () {
    if (isMobile) return false;
    $userDropdown.show();
  }).on('mouseleave', function () {
    $userDropdown.hide();
  });

  $language.on('mouseenter', function () {
    if (isMobile) return false;
    $languageDropup.show();
  }).on('mouseleave', function () {
    $languageDropup.hide();
  });

  // star
  let t = [];

  $starSelect.map(function (index, ele) {
    $(ele).on('mouseenter', '.fa', function (e) {
      clearTimeout(t[index]);
      const target = $(e.target);
      target.nextAll().removeClass('fa-star').addClass("fa-star-o");
      target.prevAll().removeClass("fa-star-o").addClass('fa-star');
      target.removeClass("fa-star-o").addClass('fa-star');
    }).on('mouseleave', '.fa', function (e) {
      const target = $(e.target);
      const active = target.parent().find('.active');
      active.nextAll().removeClass('fa-star').addClass('fa-star-o');
      active.prevAll().removeClass('fa-star-o').addClass('fa-star');
      active.removeClass('fa-star-o').addClass('fa-star');

      if (active.length === 0) {
        t[index] = setTimeout(function () {
          target.removeClass('fa-star').addClass('fa-star-o');
          target.siblings().removeClass('fa-star').addClass('fa-star-o');
        }, 200);
      }
    }).on('click', '.fa', function (e) {
      const target = $(e.target);
      target.addClass('active').siblings().removeClass('active');
      target.attr('style', 'color: #7e7c7f');
      target.prevAll().css('color', '#7e7c7f');
      target.nextAll().css('color', '#7e7c7f');
    });
  });

  // chat container scroll preventDefault
  const ele = document.querySelector('.js-slider-content');
  ele.addEventListener('mousewheel', e => {
    const self = e.currentTarget;
    const scrollTop = self.scrollTop;
    const scrollHeight = self.scrollHeight;
    const height = self.clientHeight;
    const delta = (e.wheelDelta) ? e.wheelDelta : -(e.detail || 0);
    if ((delta > 0 && scrollTop <= delta) || (delta < 0 && scrollHeight - height - scrollTop <= -1 * delta)) {
      self.scrollTop = delta > 0 ? 0 : scrollHeight;
      event.preventDefault();
    }
  }, false);

});



